@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: #007aff;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}
.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}
.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-3d, .swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}
.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}
.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}
.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}
.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}
.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}
.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}
.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}
.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "OpenSans-Regular", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  line-height: 1.3;
  color: #333333;
  background-color: #fff;
}

main {
  flex-grow: 1;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1630px;
  width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site {
  overflow: hidden;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: transparent;
  color: inherit;
  cursor: pointer;
}

.link-ul {
  position: relative;
  transition: color 0.2s;
  z-index: 2;
  transition: color 0.2s, filter 0.2s;
}
.link-ul:focus::after {
  outline: 0;
  position: absolute;
  content: "";
  display: block;
  top: -8px;
  left: -6px;
  right: -6px;
  bottom: -8px;
  background-color: #7943a4;
  color: #fff;
  z-index: -1;
  transition: color 0.2s, filter 0.2s;
}
.link-ul:hover {
  color: #c283f3;
}
.link-ul:hover::before {
  filter: invert(19%) sepia(30%) saturate(7421%) hue-rotate(217deg) brightness(92%) contrast(107%);
}
.link-ul:active {
  outline: 0;
  color: #7943a4;
}
.link-ul:active::before {
  filter: invert(69%) sepia(18%) saturate(2784%) hue-rotate(236deg) brightness(98%) contrast(91%);
}
.link-ul:active::after {
  background-color: transparent;
}

.header {
  max-height: 100px;
  position: relative;
}
.header__top {
  padding: 30px 0;
  background-color: #000;
}
.header__top-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__bottom {
  position: absolute;
  padding: 23px 0 21px;
  left: 0;
  right: 0;
  top: 100px;
  background: rgba(0, 0, 0, 0.4);
}

.nav__list {
  display: flex;
  padding-right: 150px;
}
.nav__item:not(:last-child) {
  margin-right: 62px;
}
.nav__link {
  font-weight: 600;
  color: #fff;
}

.exit-btn {
  position: relative;
  padding-left: 22px;
  color: #fff;
}
.exit-btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  display: block;
  width: 13px;
  height: 13px;
  background-image: url("../images/sprite.svg#icons--exit");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: color 0.2s, filter 0.2s;
}

.selection-menu__list {
  display: flex;
  align-items: center;
}
.selection-menu__item {
  position: relative;
}
.selection-menu__item:not(:last-child) {
  margin-right: 96px;
}
.selection-menu__btn {
  position: relative;
  padding-right: 15px;
  color: #fff;
}
.selection-menu__btn::before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: 10px;
  height: 5px;
  background-image: url("../images/sprite.svg#icons--arrow-bottom");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 10;
  transition: color 0.2s, filter 0.2s;
}

.list-scroll {
  display: none;
  position: absolute;
  top: 42px;
  padding: 40px;
  width: 300px;
  height: 300px;
  background-color: rgba(0, 0, 0, 0.6);
}
.list-scroll__item:not(:last-child) {
  margin-bottom: 15px;
}
.list-scroll__link {
  position: relative;
  display: block;
  width: 100%;
  padding: 10px 10px 12px 14px;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
  border: 2px solid transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: border 0.2s;
}
.list-scroll__link:focus {
  border: 2px solid #9d5cd0;
}
.list-scroll__link:hover {
  border: 2px solid #9d5cd0;
}
.list-scroll__link:active {
  border: 2px solid #d1a9f0;
}
.list-scroll__link:active::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background: linear-gradient(0deg, rgba(194, 131, 243, 0.4), rgba(194, 131, 243, 0.4)), url(.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 10;
}

.hero {
  height: 89.2vh;
  background-image: url("../images/bg/main-bg1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}